import React, { useEffect } from 'react'
import { Grid, Typography } from "@mui/material"
import { AI_event_user } from 'API/eventUserAPI'
import HeaderWrapper from 'components/Wrapper/HeaderWrapper'
import { useInstance } from 'easy-redux-http-call'
import { formatDate } from 'utils'
import { makeStyles } from '@mui/styles'
import { IconButton } from "@mui/material"
import LaunchIcon from '@mui/icons-material/Launch';
import DeleteIcon from '@mui/icons-material/Delete';
import { useNavigate } from 'react-router'
import { GroupChip } from 'containers/Setting/UserGroups'

const useStyles = makeStyles((theme) => ({
  usersContainer: {
    display: "grid",
    gridTemplateColumns: "1",
    gridGap: 8,
  },
  dmUserCard: {
    background: theme.palette.primary.main + "24",
    flex: 1,
    padding: 10,
    borderRadius: theme.shape.borderRadius,
    position: "relative"
  },
  dmViewActionContainer: {
    position: 'absolute',
    top: 6,
    right: 6
  },
  dmGroupContianer: {
    width: "fit-content",
    display: "inline-block",
    "&>div": {
      marginRight: 4,
    }
  }
}))

export default function Users() {
  const eventUserData = useInstance(AI_event_user)
  const classes = useStyles()
  const navigator = useNavigate()

  useEffect(() => {
    AI_event_user.call()
  }, [])

  const viewDetailProfile = (id) => {
    navigator(`/person/view/${id}`)
  }

  return (
    <HeaderWrapper backAction title="Users">
      <Grid className={classes.usersContainer}>
        {
          eventUserData?.data?.data.map(i =>
            <Grid className={classes.dmUserCard}>
              <Typography variant='h6'>
                <b>Name: </b>
                {i.name}
              </Typography>
              <Typography>
                <b>Created At: </b>
                {formatDate(i.createdAt)}
              </Typography>
              <Typography>
                <b>Total events: </b>
                {i?.events?.length}
              </Typography>
              <Typography>
                <b>Groups: </b>
                <Grid className={classes.dmGroupContianer}>
                  {i?.group?.map((group) =>
                    <GroupChip
                      group={group}
                      key={group._id}
                    />
                  )}
                </Grid>
              </Typography>
              <div className={classes.dmViewActionContainer}>
                <IconButton size="small" onClick={() => viewDetailProfile(i._id)} disabled>
                  <DeleteIcon />
                </IconButton>
                <IconButton size="small" onClick={() => viewDetailProfile(i._id)}>
                  <LaunchIcon />
                </IconButton>
              </div>
            </Grid>
          )
        }
      </Grid>
    </HeaderWrapper>
  )
}
